import React, { Component } from 'react';

export default class Cell extends Component{
    constructor(props){
        super(props);

        this.state({
            cellKey: props.key,
        });
    }


    render(){
        return(
            <div className="cell" key={ this.state.cellKey }>
                {/*{this.state.grid[i][j]}*/}
                {this.state.cellKey}
            </div>
        );
    }
}